}

.responsive-expander::before {
  -webkit-transition: 0.3s -webkit-transform;
  transition: 0.3s -webkit-transform;
  transition: 0.3s transform;
  transition: 0.3s transform, 0.3s -webkit-transform
}

.resonsive-expandable {
  -webkit-transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);
  transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1)
}

.responsive-expanded .responsive-expander::before {
  -webkit-transform: scaleY(-1);
  transform: scaleY(-1)
}

.responsive-expanded .responsive-expandable {
  max-height: 100000px;
  overflow: hidden;
  opacity: 1;
  -webkit-transition: max-height 0.3s cubic-bezier(0.075, 0.82, 0.165, 1),opacity 0.5s;
  transition: max-height 0.3s cubic-bezier(0.075, 0.82, 0.165, 1),opacity 0.5s
}

.responsive-expanded .responsive-expandable * {
  max-height: 100000px;
  opacity: 1;
  -webkit-transition: max-height 0.3s, opacity 0.5s;
  transition: max-height 0.3s, opacity 0.5s
}

.responsive-collapsed .responsive-expander::before {
  -webkit-transform: scaleY(1);
  transform: scaleY(1)
}

.responsive-collapsed .responsive-expandable {
  max-height: 0;
  opacity: 0;
  visibility: hidden;
  -webkit-transition: max-height 0.25s cubic-bezier(0.075, 0.82, 0.165, 1),visibility 0s 0.3s,opacity 0.2s;
  transition: max-height 0.25s cubic-bezier(0.075, 0.82, 0.165, 1),visibility 0s 0.3s,opacity 0.2s
}

.responsive-collapsed .responsive-expandable * {
  max-height: 0;
  opacity: 0;
  padding: 0;
  -webkit-transition: max-height 0.25s, padding 1s, opacity 0.2s;
  transition: max-height 0.25s, padding 1s, opacity 0.2s
}

